import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './Alert.css';

const Alert = ({ arrowLeftPosition, hasArrow, isError, text }) => {
  let specialArrow;
  if (parseFloat(arrowLeftPosition) <= 4) {
    specialArrow = '_hasSpecialArrowStart';
  } else if (parseFloat(arrowLeftPosition) >= 95) {
    specialArrow = '_hasSpecialArrowEnd';
  }
  if (parseFloat(arrowLeftPosition) >= 100) {
    arrowLeftPosition = '100%';
  }
  const classes = classNames('Alert', specialArrow, {
    _hasArrow: hasArrow,
    _error: isError
  });
  return (
    <div className={classes} style={{ '--alertAfterLeft': arrowLeftPosition }}>
      {isError && <strong>Error:</strong>} {text}
    </div>
  );
};

Alert.propTypes = {
  arrowLeftPosition: PropTypes.string,
  hasArrow: PropTypes.bool,
  isError: PropTypes.bool,
  text: PropTypes.node.isRequired
};

Alert.defaultProps = {
  arrowLeftPosition: '0%',
  hasArrow: true,
  isError: false
};

export default Alert;



