import PropTypes from 'prop-types';
import React from 'react';
import { TOTAL_FUND_GOAL } from 'constants/app';
import './Progress.css';

const Progress = ({ fundedAmount }) => (
	<div className="Progress">
		<progress max={TOTAL_FUND_GOAL} value={fundedAmount} />
	</div>
);

Progress.propTypes = {
	fundedAmount: PropTypes.number
};

Progress.defaultProps = {
	fundedAmount: 0
};

export default Progress;



