import React, { Component } from 'react';
import Alert from 'components/Alert';
import classNames from 'classnames';
import numeral from 'numeral';
import Progress from 'components/Progress';
import {
  DEFAULT_DONOR_AMOUNT,
  DEFAULT_FUND_AMOUNT,
  DEFAULT_FUNDED_AMOUNT,
  ERRORS,
  LOWEST_FUND_AMOUNT,
  TOTAL_FUND_GOAL
} from 'constants/app';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);
    this.FundAmountInput = React.createRef();
    this.Confetti = React.createRef();

    // If there is a state stored in localStorage, lets use that
    if (localStorage.state) {
      this.state = JSON.parse(localStorage.getItem('state'));
    } else {
      this.state = {
        Donors: DEFAULT_DONOR_AMOUNT,
        Fund: numeral(DEFAULT_FUND_AMOUNT).format('0.00'),
        FundedAmount: DEFAULT_FUNDED_AMOUNT,
        GoalReached: false,
        InputIsFocused: false,
        ValidationError: false
      };
    }
  }

  componentDidMount() {
    // This is just for the demo to clear your local storage with CTRL + Z
    document.addEventListener('keydown', this._clearLocalStorage, false);
  }

  componentWillUnmount() {
    // This is just for the demo to clear your local storage with CTRL + Z
    document.removeEventListener('keydown', this._clearLocalStorage, false);
  }

  componentDidUpdate() {
    localStorage.state = JSON.stringify(this.state);
    if (!this.state.GoalReached && this.state.FundedAmount >= TOTAL_FUND_GOAL) {
      this._confettiTimer();
      this.setState({
        GoalReached: true
      });
    }
  }

  // This is just for the demo to clear your local storage with CTRL + Z
  _clearLocalStorage(e) {
    if (e.ctrlKey && e.keyCode === 90) {
      console.log('test');
      localStorage.clear();
      window.location.reload(true);
    }
  }

  _confettiTimer = () =>
    setTimeout(() => {
      this.Confetti.current.classList.remove('_show');
    }, 1500);

  _getAmountLeftText = () => {
    const amountLeft = TOTAL_FUND_GOAL - this.state.FundedAmount;
    if (this.state.GoalReached) {
      return (
        <span>
          <strong>Project successfully funded!</strong> Thank you for your
          donation!{' '}
          {amountLeft !== 0 && (
            <strong>
              <sup>$</sup>
              {numeral(Math.abs(amountLeft)).format('0,0.00')} over our goal!
            </strong>
          )}
        </span>
      );
    } else {
      return (
        <span>
          <strong>
            <sup>$</sup>
            {numeral(amountLeft).format('0,0.00')}
          </strong>{' '}
          still needed to fund this project.
        </span>
      );
    }
  };

  _onHandleSubmit = e => {
    clearTimeout(this.confettiTimerID);
    let fundAmount = this.state.Fund;

    // Reset any errors
    if (this.state.ValidationError) {
      this.setState({
        Fund: numeral(LOWEST_FUND_AMOUNT).format('0.00'),
        ValidationError: false
      });

      // Make sure the input has a number and/or isn't blank
    } else if (isNaN(fundAmount) || fundAmount === '') {
      this.setState({
        Fund: numeral(DEFAULT_FUND_AMOUNT).format('0.00'),
        ValidationError: ERRORS.NAN
      });

      // Biz rule: Donation must be at least $5
    } else if (fundAmount < LOWEST_FUND_AMOUNT) {
      this.setState({
        Fund: numeral(LOWEST_FUND_AMOUNT).format('0.00'),
        ValidationError: ERRORS.INSUFFICIENT
      });

      // Everything checks out
    } else {
      fundAmount = parseFloat(fundAmount);

      this.setState(prevState => ({
        Donors: prevState.Donors + 1,
        Fund: numeral(fundAmount).format('0.00'),
        FundedAmount: prevState.FundedAmount + fundAmount,
        ValidationError: false
      }));

      // We don't want any special animation now the goal is complete
      if (!this.state.GoalReached) {
        this.Confetti.current.classList.add('_show');
        this.confettiTimerID = this._confettiTimer();
      }
    }

    // Re-focus on the input
    this.FundAmountInput.current.focus();
    e.preventDefault();
  };

  _onHandleInputChange = e => {
    let fundAmount = e.target.value;

    this.setState({ Fund: fundAmount });

    // Make sure the input has a number and/or isn't blank
    if (isNaN(fundAmount) || fundAmount === '') {
      this.setState({ ValidationError: ERRORS.NAN });

      // Biz rule: Donation must be at least $5
    } else if (fundAmount < LOWEST_FUND_AMOUNT) {
      this.setState({ ValidationError: ERRORS.INSUFFICIENT });

      // Everything checks out
    } else {
      this.setState({ ValidationError: false });
    }
  };

  _handleOnBlur = () => {
    this.setState({ InputIsFocused: false });
  };

  _handleOnFocus = () => {
    this.setState({ InputIsFocused: true });
  };

  render() {
    const {
      Donors,
      Fund,
      FundedAmount,
      GoalReached,
      InputIsFocused,
      ValidationError
    } = this.state;
    return (
      <div className="App">
        <Alert
          text={this._getAmountLeftText()}
          arrowLeftPosition={numeral(FundedAmount / TOTAL_FUND_GOAL).format(
            '0.00%'
          )}
        />
        <div className="Card">
          <Progress fundedAmount={FundedAmount} />
          <div className="CardBody">
            {!GoalReached && <h1>Only four days left to fund this project</h1>}
            {GoalReached && (
              <h1>
                We've met our goal of ${numeral(TOTAL_FUND_GOAL).format('0,0')},
                but you still have four days left to contribute even more!
              </h1>
            )}
            <p>
              Join the <strong>{Donors}</strong> other donors who have already
              supported this project.
            </p>
            <form
              className={classNames('Form', {
                _isFocused: InputIsFocused
              })}
              onSubmit={this._onHandleSubmit}
            >
              <label htmlFor="DonationAmount" className="InputPrepend">
                <span>$</span>
              </label>
              <input
                aria-label="Donation Amount"
                autoFocus={true}
                className="Input"
                id="DonationAmount"
                ref={this.FundAmountInput}
                onBlur={this._handleOnBlur}
                onChange={this._onHandleInputChange}
                onFocus={this._handleOnFocus}
                step="0.01"
                type="number"
                value={Fund}
              />
              <div className="InputAppend">
                <button className="Button" type="submit">
                  Give Now
                </button>
              </div>
            </form>
            {ValidationError && (
              <Alert isError hasArrow={false} text={ValidationError} />
            )}
          </div>
        </div>
        <p className="_demo">
          For demo purposes, press <kbd>CTRL+Z</kbd> to clear localStorage
        </p>
        <div
          id="Confetti"
          ref={this.Confetti}
          style={{
            opacity: GoalReached ? 1 : 0
          }}
        />
      </div>
    );
  }
}

export default App;



